.who__can__learn {
  background-color: #e8f1f9;
  position: relative;
  overflow: hidden;
  max-width: 1920px;
  margin: 0 auto;
  padding: 0 315px; }
  .who__can__learn:before {
    content: '';
    position: absolute;
    width: 180px;
    height: 180px;
    filter: blur(5px);
    -o-filter: blur(5px);
    -ms-filter: blur(5px);
    -moz-filter: blur(5px);
    -webkit-filter: blur(5px);
    opacity: 0.3;
    bottom: -55px;
    left: -30px;
    background-size: cover;
    background-image: url("../../../img/header-ball.png"); }
  .who__can__learn:after {
    content: '';
    position: absolute;
    width: 180px;
    height: 180px;
    filter: blur(5px);
    -o-filter: blur(5px);
    -ms-filter: blur(5px);
    -moz-filter: blur(5px);
    -webkit-filter: blur(5px);
    opacity: 0.3;
    top: 15px;
    right: -70px;
    background-size: cover;
    background-image: url("../../../img/header-ball.png"); }
  .who__can__learn .who__can__lear__header {
