        color: #245ff7; }
    .who__can__learn .who__can__lear__header p {
      font-size: 24px;
      line-height: 130%;
      font-weight: normal; }
  .who__can__learn .content {
    display: flex;
    justify-content: space-between; }
    .who__can__learn .content__block {
      display: flex;
